body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.ntext1 {
  color: black  !important ;

}
.section-header h1 {
  font-size: 48px;
  font-weight: 600;
  letter-spacing: 0.05em;
  color:blanchedalmond;
}
.section-header1 h2 {
  font-size: auto;
  font-weight: 600;
  letter-spacing: 0.05em;
  color:lightseagreen !important;
}
h1 ,h2{
  font-family: 'Roboto Slab',serif;
  font-weight: 400;
  letter-spacing: 0.02em;
  margin: 0 0 20px;
}
.row1 {
  margin-right: -15px;
  margin-left: -15px;
}
.row1 {
  --bs-gutter-x: 0;
  --bs-gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(var(--bs-gutter-y) * -1);
  margin-right: calc(var(--bs-gutter-x)/ -2);
  margin-left: calc(var(--bs-gutter-x)/ -2);
}
.section-header1 h1 {
  font-size: 48px;
  font-weight: 600;
  letter-spacing: 0.05em;
  color:black;
}
.item:hover .icon{
  /* -webkit-transform:translateY(-10px); */
  transform:translateY(-10px);
  -webkit-transition:-webkit-transform .4s ease-out;
  transition:transform .4s ease-out;
}

.item {
  text-align:center;
  margin-bottom:30px;
}

 .item .icon{
  margin:0 auto 30px;
  -webkit-transition:all .3s ease-out;
  transition:all .3s ease-out;
}


.item h2{
  font-size:24px;
  -webkit-transition:all .3s ease-out;
  transition:all .3s ease-out;
}
 .item:hover h2{
  color:#38A5DB;
}
.scrollimation {
  opacity: 1;
  -webkit-transform: translateY(0px);
  -moz-transform: translateY(0px);
  -ms-transform: translateY(0px);
  transform: translateY(0px);
}
.row {
  margin-right: -15px;
  margin-left: -15px;
}
.navlink{
  cursor: pointer;
  padding: auto;
  margin-left: 50px ;
  font-size: 15px;
  line-height: 54px;
  font-weight: 400;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  background: transparent;
  color: #fff;
}
.navlink::after {
  content: "";
  bottom: -4px;
  left: 0;
  height: 2px;
  width: 0%;
  background-color:black;
  display: block;
}

.navlink:hover::after {
  width: 100%;
  transition: 0.3s ease all;
}
.scrolled {
  background: #fff;
  box-shadow: 0 5px 5px rgb(125 136 140);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer-dark {
  padding:50px 0;
  color:#f0f9ff;
  background-color:#282d32;
}

.footer-dark h3 {
  margin-top:0;
  margin-bottom:12px;
  font-weight:bold;
  font-size:16px;
}

.footer-dark ul {
  padding:0;
  list-style:none;
  line-height:1.6;
  font-size:14px;
  margin-bottom:0;
}

.footer-dark ul a {
  color:inherit;
  text-decoration:none;
  opacity:0.6;
}

.footer-dark ul a:hover {
  opacity:0.8;
}

@media (max-width:767px) {
  .footer-dark .item:not(.social) {
    text-align:center;
    padding-bottom:20px;
  }
}

.footer-dark .item.text {
  margin-bottom:36px;
}

@media (max-width:767px) {
  .footer-dark .item.text {
    margin-bottom:0;
  }
}

.footer-dark .item.text p {
  opacity:0.6;
  margin-bottom:0;
}

.footer-dark .item.social {
  text-align:center;
}

@media (max-width:991px) {
  .footer-dark .item.social {
    text-align:center;
    margin-top:20px;
  }
}

.footer-dark .item.social > a {
  font-size:20px;
  width:36px;
  height:36px;
  line-height:36px;
  display:inline-block;
  text-align:center;
  border-radius:50%;
  box-shadow:0 0 0 1px rgba(255,255,255,0.4);
  margin:0 8px;
  color:#fff;
  opacity:0.75;
}

.footer-dark .item.social > a:hover {
  opacity:0.9;
}

.footer-dark .copyright {
  text-align:center;
  padding-top:24px;
  opacity:0.3;
  font-size:13px;
  margin-bottom:0;
}


.bg {
  /* The image used */
  background-image: url("https://wpamelia.com/wp-content/uploads/2018/10/Webp.net-resizeimage-1.jpg");

  /* Full height */
  height: 100%; 

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  -webkit-animation: fadeIn 3s;
  animation: fadeIn 3s;
  opacity: 1;
  background-color: #404040;
  background-blend-mode: overlay;
}

